/**
 *
 *  
 * @export
 * @param {*} response  isReject true   config.errorMsg  
 * @param {*} isReject resolve  reject  400  500  reject 
 * @param { Object } origin  { request, response, config }
 */
// eslint-disable-next-line
export function message (response, isReject = false, origin = {}) {
  if (isReject) {
    //  response    notify Bug
    rrc && rrc.$notify.error(Object.assign({}, response))
    return
  }
  const { data } = response
  rrc && rrc.$notify.error({
    title: '',
    message: data.message
  })
}
