import { isUndefined } from '@rrc/utils'
/**
 *
 * 
 *  transformResponse 
 * @export
 * @param {*} response
 * @return {boolbean} true 
 */
export function autoMessageAdapter (response) {
  const { data: { code } } = response
  //  code   transformResponse   isAutoMsg 
  if (isUndefined(code)) {
    return false
  }
  if (code !== 0) {
    return true
  }
  return false
}
